import { treeDots, refresh } from './svg.js';

import '../styles/style.css';

const createTaskElement = (task) => {
  const taskElement = document.createElement('li');
  taskElement.className = `task ${task.completed ? 'completed' : ''}`;
  taskElement.innerHTML = `
      <input type="checkbox" class="completed-checkbox" ${task.completed ? 'checked' : ''}>
      <span class="task-description">${task.description}</span>
      <button class="treeDots">${treeDots}</button>
  `;
  
  return taskElement;
};
class TaskManager {
    constructor() {
        this.tasks = JSON.parse(localStorage.getItem('tasks')) || [];
        this.taskList = document.getElementById('task-list');
        
        this.renderTasks();
    }
    
    saveTasksToLocalStorage() {
        localStorage.setItem('tasks', JSON.stringify(this.tasks));
    }
    
    renderTasks() {
        this.taskList.innerHTML = '';
        
        this.tasks.forEach((task, index) => {
            const taskElement = createTaskElement(task, index);
            this.taskList.appendChild(taskElement);
        });
        
        this.saveTasksToLocalStorage();
    }
    
    updateTaskIndexes() {
        this.tasks.forEach((task, index) => {
            task.index = index + 1;
        });
    }

    checkbox (target) {
      if (target.checked) {
        target.parentNode.classList.add('completed');
      } else {
        target.parentNode.classList.add('completed');
      }
    }
    
    addTask(description) {
        const newTask = {
            description: description,
            completed: false,
            index: this.tasks.length + 1,
        };
        this.tasks.push(newTask);
        this.renderTasks();
    }
    
    deleteTask(index) {
        this.tasks.splice(index, 1);
        this.updateTaskIndexes();
        this.renderTasks();
    }
    
    updateTaskCompletion(index, completed) {
        this.tasks[index].completed = completed;
        this.saveTasksToLocalStorage();
        this.renderTasks();
    }
}

export default TaskManager;